.hero {
    position: relative;
    background-color: $color-prussian-blue;
    padding: 210px 0 283px 95px;

    @include vp-1023 {
        padding: 0;
        padding-top: 235px;
        padding-bottom: 280px;
    }

    @include vp-767 {
        padding: 143px 25px 185px 0;
    }


    &__background {
        width: 100%;
        min-height: 730px;

        position: absolute;
        top: -108px;
        left: 0;
        z-index: 1;

        @include vp-1023 {
            min-height: 727px;
        }
    }

    &__content {
        position: relative;
        z-index: 2;

        font-size: 20px;
        line-height: 24px;
        color: $color-alice-blue;
        text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    }

    &__title {
        margin: 0;
        margin-bottom: 30px;

        font-weight: 700;
        font-size: 54px;
        line-height: 66px;

        @include vp-1023 {
            font-size: 50px;
            line-height: 61px;
        }

        @include vp-767 {
            font-size: 36px;
            line-height: 45px;

            margin-bottom: 25px;
        }
    }

    &__text {
        margin: 0;

        @include vp-1023 {
            font-size: 18px;
            line-height: 22px;
        }
    }
}